// ============================================
// APP LAYOUT (Sidebar)
// ============================================
interface AppLayoutProps {
  children: React.ReactNode;
  activePage: string;
  wallet: string | null;
  onConnect: () => void;
  onNavigate: (path: string) => void;
}

const AppLayout = ({ children, activePage, wallet, onConnect, onNavigate }: AppLayoutProps) => {
  return (
    <div className="app-layout">
      <aside className="sidebar">
        <button onClick={() => onNavigate('landing')} className="sidebar-logo">Fangorn</button>
        <div className="sidebar-sub">Liquid Access Rights</div>
        <nav className="nav-items">
          <button 
            onClick={() => onNavigate('dashboard')} 
            className={`nav-item ${activePage === 'dashboard' ? 'active' : ''}`}
          >
            Dashboard
          </button>
          <button 
            onClick={() => onNavigate('library')} 
            className={`nav-item ${activePage === 'library' ? 'active' : ''}`}
          >
            My Library
          </button>
        </nav>
        <div className="sidebar-footer">
          {wallet ? (
            <>
              <div className="wallet-status">Connected</div>
              <div className="wallet-address">{wallet}</div>
            </>
          ) : (
            <button className="btn-primary sidebar-connect" onClick={onConnect}>
              Connect Wallet
            </button>
          )}
        </div>
      </aside>
      <main className="main-content">{children}</main>
    </div>
  );
};

