// ============================================
// MAIN PAGE COMPONENT
// ============================================
export default function Page() {
  const [route, setRoute] = useState('landing');
  const [assetId, setAssetId] = useState<string | null>(null);
  const [wallet, setWallet] = useState<string | null>(null);

  const connectWallet = () => {
    setWallet('0x7a3d...f82c');
  };

  const navigate = (path: string) => {
    if (path.startsWith('asset/')) {
      setAssetId(path.split('/')[1]);
      setRoute('asset');
    } else {
      setRoute(path);
    }
    window.scrollTo(0, 0);
  };

  return (
    <>
      {route === 'landing' && <LandingPage onNavigate={navigate} />}
      {route === 'dashboard' && <DashboardPage wallet={wallet} onConnect={connectWallet} onNavigate={navigate} />}
      {route === 'library' && <LibraryPage wallet={wallet} onConnect={connectWallet} onNavigate={navigate} />}
      {route === 'asset' && <AssetPage assetId={assetId} wallet={wallet} onConnect={connectWallet} onNavigate={navigate} />}
    </>
  );
}
